.Modal {
    position: fixed;
    top: 0; bottom: 0; left: 0; right: 0;
    width: 50%;
    margin: auto;
    height: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: white;
    border-radius: 5px;
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, .8);
            box-shadow: 0 10px 20px rgba(0, 0, 0, .8);
}

.Modal-title {
    position: absolute;
    padding: 15px 0px;
    top: 0;
    width: 100%;
    font-family: 'Jua', sans-serif;
    font-size: 40px;
    background: gold;
    border-radius: 5px 5px 0px 0px;
    /* border: 2px solid #222; */
}

.Modal-sectionTitle {
    font-size: 30px;
    margin-bottom: 5px;
    margin-top: 90px;
}

.Modal-difficultyLabelContainer {
    margin-top: 5px;
}

.Modal-difficultyLabel {
    margin: 5px 10px;
}

@media screen and (max-width: 768px) {
    .Modal {
        width: 80%;
    }
}


